import React from 'react';
import spinner from './spinner.gif'

 const Spinner =()=> {
  return (
    <div style={{position:"fixed",top:"30%",left:"50%"}}>
        <img src={spinner} 
        style={{width:'100px', margin:'auto', display:'block'}}
        alt="Loading..."
        />
    </div>
  )
}
export default Spinner